#header {
    position: absolute;
    width: 220px;
  }
  #header .ProSidebarProvider {
    height: 100vh;
  }
  #header .closemenu {
    color: #000;
    position: absolute;
    right: 0;
    z-index: 9999;
    line-height: 20px;
    border-radius: 50%;
    font-weight: bold;
    font-size: 22px;
    top: 55px;
    cursor: pointer;
  }
  #header .ProSidebarProvider {
    width: 100%;
    min-width: 100%;
  }
  #header .ProSidebarProvider.collapsed {
    width: 80px;
    min-width: 80px;
  }
  #header .ProSidebarProvider-inner {
    background-color: white;
    box-shadow: 0.5px 0.866px 2px 0px rgba(0, 0, 0, 0.15);
  }
  #header .ProSidebarProvider-inner .ProSidebarProvider-layout {
    overflow-y: hidden;
  }
  #header .ProSidebarProvider-inner .ProSidebarProvider .logotext p {
    font-size: 20px;
    padding: 0 20px;
    color: #000;
    font-weight: bold;
  }
  #header .ProSidebarProvider-inner .ProSidebarProvider-layout ul {
    padding: 0 5px;
  }
  #header .ProSidebarProvider-inner .ProSidebarProvider-layout ul .pro-inner-item {
    color: #000;
    margin: 10px 0px;
    font-weight: bold;
  }
  #header .ProSidebarProvider-inner .ProSidebarProvider-layout ul .pro-inner-item .pro-icon-wrapper {
    background-color: #fbf4cd;
    color: #000;
    border-radius: 3px;
  }
  #header .ProSidebarProvider-inner .ProSidebarProvider-layout ul .pro-inner-item .pro-icon-wrapper .pro-item-content {
    color: #000;
  }
  #header .ProSidebarProvider-inner .ProSidebarProvider-layout .active {
    background-image: linear-gradient(0deg, #fece00 0%, #ffe172 100%);
  }
  #header .Ink {
    padding: 20px;
  }
  
  @media only screen and (max-width: 720px) {
    html {
      overflow: hidden;
    }
  }